import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			initData: {},
			expressData: {}
		};
	},
	methods: {
		linkToDelivery(orderId){
			uni.navigateTo({
				url: '/pages/delivery/delivery?orderId=' + orderId
			})
		},
		linkToPayment(order){
			let payOrder = [];
			order.map(item => {
				payOrder.push({
					goodsCount: item.goodsCount,
					mallGoodsId: item.mallGoodsId
				})
			});
			uni.setStorage({
				key:'buyList',
				data:payOrder,
				success: () => {
					uni.navigateTo({
						url:'/pages/payment/payment'
					})
				}
			})
		},
		confirmOrder(orderId){
			this.$http.post('/mall/app/order/receive', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					uni.showToast({
						icon: 'none',
						title: '确认收货成功'
					})
					this.loadData(orderId);
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		cancelOrder(orderId){
			uni.showModal({
			    title: '提示',
			    content: '确定取消订单吗？',
				confirmColor: '#E93B3D',
			    success: res => {
			        if (res.confirm) {
			            this.$http.post('/mall/app/order/cancel', {
							...this.baseInfo,
							accountId: this.userInfo.accountId,
							orderId: orderId
						})
						.then( res => {
							if(res.code == 0){
								uni.showToast({
									icon: 'none',
									title: '取消订单成功'
								})
								this.loadData(orderId);
							}else{
								uni.showToast({
									icon: 'none',
									title: '取消订单失败'
								})
								this.loadData();
							}
						})
						.catch( err => {
							console.log(err);
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		transformStatus(num){
			switch(num){
				case -1:
					return '待查询';
					break;
				case 0:
					return '查询异常';
					break;
				case 1:
					return '暂无记录';
					break;
				case 2:
					return '在途中';
					break;
				case 3:
					return '派送中';
					break;
				case 4:
					return '已签收';
					break;
				case 5:
					return '用户拒签';
					break;
				case 6:
					return '疑难件';
					break;
				case 7:
					return '无效单';
					break;
				default:
					return '';
			}
		},
		initExpress(orderId){
			this.$http.post('/mall/app/order/express', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					this.expressData=JSON.parse(res.result.result).showapi_res_body;
					this.expressData.statusName=this.transformStatus(this.expressData.status);
					this.expressData.data=this.expressData.data[0];
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		loadData(orderId){
			this.$http.post('/mall/app/order/detail', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					this.initData=res.result;
				}
			})
			.catch( err => {
				console.log(err);
			})
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo'])
	},
	onLoad(options){
		this.loadData(options.orderId);
		this.initExpress(options.orderId);
	}
}
